@import url("https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap");

.creative {
  font-family: "Ubuntu", sans-serif;
  font-weight: 500;
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  // background-image: url('./bg.png');
  background-repeat: no-repeat;
  background-position: bottom;


  div {
    margin-bottom: 3px;
  }


  .display-flex {
    display: flex;
    flex-direction: row;
  }

  .justify-align-center {
    align-items: center;
    justify-content: center;
  }

  .space-between {
    justify-content: space-between;
  }

  .small-italic-gray {
    font-style: italic;
    color: rgb(85, 84, 84);
    font-size: 12px;
    font-weight: 400;
  }

  .header {
    background-color: #173042;
    color: whitesmoke;
    padding: 40px 30px;
    padding-bottom: 50px;
    margin: -31px;

    .name {
      font-size: 32px;
      font-weight: 500;
      margin-bottom: 0px;
      text-transform: uppercase;
    }

    .title {
      font-size: 16px;
      font-weight: 400;
      margin-bottom: 10px;
    }

    .description {
      font-size: 12px;
    }
  }

  .ribbon {
    //   width: 100vw;
    padding: 10px;
    padding-left: 40px;
    padding-bottom: 8px;
    background-color: #0b1829;
    margin: 0 -40px;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    font-size: 12px;
    color: white;

    .item {
      display: flex;
      align-items: center;
      // justify-content: center;
      margin-right: 25px;
      width: 45%;
      padding: 5px;

      .icon {
        width: 14px;
        height: 14px;
        margin-right: 10px;
        filter: invert(1);
      }

      .text {
        margin-bottom: 0px;

        a {
          text-decoration: none !important;
          color: white;

        }
      }
    }
  }

  .col-group {
    width: 100%;
    display: flex;

    .column {
      display: flex;
      flex-direction: column;
      padding-top: 5px;
      flex: 1;

      &:first-child {
        margin-right: 25px;
      }
    }
  }

  .section {
    padding: 5px;
    padding-top: 5px;
    padding-bottom: 5px;
    margin-top: 5px;
    margin-bottom: 10px;
    font-weight: 500;
    flex-direction: column;

    .title {
      font-size: 20px;
      font-weight: 700;
      text-transform: uppercase;
      color: #607d8b;
      margin-bottom: 8px;
    }
  }

  .skills {
    display: flex;
    flex-wrap: wrap;
    font-size: 12px;
    font-weight: 500;

    .skill {
      border-radius: 4px;
      background: #999da6;
      color: white;
      margin: 2px 3px;
      padding: 6px;
    }
  }

  .achievements {
    .achievement {
      .title {
        font-size: 16px;
        font-weight: 700;
        margin: 0;
        text-transform: none;
        color: black;
      }

      .organization {
        font-weight: 400;
        font-size: 16px;
      }

      .year {
        font-style: italic;
        color: gray;
        font-size: 12px;
      }

      margin-bottom: 15px;
    }
  }

  .education-items {
    .title {
      font-size: 16px;
      font-weight: 700;
      margin: 0;
      text-transform: none;
      color: black;
    }

    .institute {
      font-weight: 400;
    }

    .year,
    .dates {
      font-style: italic;
      color: rgb(85, 84, 84);
      font-size: 12px;
      font-weight: 400;
    }

    margin-bottom: 15px;
  }

  .languages {
    display: flex;
    flex-wrap: wrap;

    .language {
      width: 50%;
      margin-bottom: 15px;

      .name {
        font-size: 14px;
      }

      .proficiency {
        font-style: italic;
        color: rgb(85, 84, 84);
        font-size: 12px;
        font-weight: 400;
      }
    }
  }

  .interests {
    display: flex;
    flex-wrap: wrap;

    .interest {
      border-radius: 4px;
      margin: 2px 3px;
      padding: 6px;
      font-size: 12px;
      border: 1px solid gray;
    }
  }

  .work-history {
    .job {
      margin-bottom: 30px;

      .role {
        font-size: 16px;
        font-weight: 700;
        margin: 0;
        text-transform: none;
        color: black;
      }

      .organization {
        font-size: 14px;
        font-weight: 500;
      }

      .responsibilities {
        font-size: 12px;
        font-weight: 400;
        padding-left: 16px;

        li {
          margin-bottom: 6px;
        }
      }
    }
  }
}