@import url("https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap");


body {
    margin: 0;
}

.main {
    font-family: "Ubuntu", sans-serif;
    font-weight: 500;
    text-rendering: optimizeLegibility !important;
    -webkit-font-smoothing: antialiased !important;
}

.main {
    display: flex;
    background: #e6e6e6;

    .template-selector {
        width: 300px;
        padding: 20px;
        box-shadow: 5px 0px 9px 1px rgb(0 0 0 / 42%);
        margin: -10px;
        margin-right: 40px;
        background-color: #f1f1f1;
        position: fixed;
        height: 100vh;

        .template-option {
            cursor: pointer;
            border-bottom: 1px solid;
            padding: 15px 10px;
            font-size: 18px;

            &.active {
                &::before {
                    content: "ðŸŸ¢  ";
                }
            }
        }
    }

    .right {
        position: relative;
        left: 400px;

        .options {
            margin-top: 20px;
            padding: 20px;

            button {
                border: 1px solid;
                outline: none;
                padding: 10px;
                min-width: 200px;
                cursor: pointer;

                &:active {
                    background-color: rgba(213, 213, 213, 0.296);
                }
            }
        }
    }

    .template {
        margin-top: 15px;
        margin-bottom: 50vh;
        padding: 17px;

        #resume-content {
            background-color: white;

            @media screen {
                box-shadow: 5px 0px 9px 1px rgb(0 0 0 / 42%);
            }

            @media print {
                box-shadow: none
            }
        }

    }
}